<!-- eslint-disable prettier/prettier -->
<template>
<div>
    <h3 class="text-2xl font-bold mb-4">Latest Posts</h3>
    <div class="container">
        <ul>
            <li v-for="(post, index) in firstTenPosts" :key="index">
                <div class="post-area bg-gray-100 p-4 mb-4 shadow-md transition duration-500 ease-in-out transform hover:scale-105 relative cursor-pointer">
                <span class="post-views text-gray-600 text-right p-2 bg-indigo-400 rounded-md absolute top-2 right-1">{{ post.views }}</span>
                <h4 class="post-title text-2xl font-bold mb-2 text-left text-black hover:text-indigo-500 transition duration-300">{{ post.title }}</h4>
                <span class="post-date text-gray-500 block mb-4 text-left">{{ post.date }}</span>
                <p class="post-content text-gray-700">
                    {{ $shorten.shorten(post.content, 50, ' . More....') }}
                </p>
                <div class="flex justify-between items-center mt-4">
                    <div class="flex items-center">
                    <span class="post-author text-white bg-gray-600 p-2 rounded-lg hover:bg-gray-700 transition duration-300">
                        {{ post.author }}
                    </span>
                    </div>
                    <div>
                    <span class="post-category text-gray-600 bg-indigo-300 p-2 rounded-lg hover:bg-indigo-400 transition duration-300">
                        {{ post.category }}

                    </span>
                    </div>
                </div>
                </div>
            </li>
        </ul>
    </div>
</div>
</template>
<!-- eslint-disable prettier/prettier -->
<script>
export default {
name: "LastPosts",
props: ["posts"], // Define a prop to receive posts from the parent
computed: {
    firstTenPosts() {
    // console.log('First ten posts:', this.posts.slice(0, 10));
    return this.posts.slice(0, 10);
    },
},
};
</script>
