<!-- eslint-disable prettier/prettier -->
<template>
    <div class="sidebar bg-gray-200 p-4">
    <!-- Categories -->
        <div class="mb-4">
            <h3 class="text-lg font-semibold mb-2 p-2 bg-indigo-200 rounded-lg">Categories</h3>
            <ul >
                <li v-for="(post, index) in firstTenPosts" :key="index">
                <a href="#" class="text-gray-600 hover:text-indigo-500 transition duration-300">{{ post.category }}</a>
            </li>
            </ul>
        </div>

    <!-- Recent Posts -->
        <div>
            <h3 class="text-lg font-semibold mb-2 p-2 bg-indigo-200 rounded-lg">Recent Posts</h3>
            <ul>
                <li v-for="(post, index) in firstTenPosts" :key="index">
                <a href="#" class="text-gray-600 hover:text-indigo-500 transition duration-300">{{ post.title }}</a>
            </li>
            </ul>
        </div>
    </div>
</template>
<!-- eslint-disable prettier/prettier -->
<script>
export default {
    name: 'SideSection',
    props: ["posts"],
    computed: {
        firstTenPosts() {
            return this.posts.slice(0, 15);
        },
    },
};
</script>
